body {
    font-family: "Book Antiqua", Palatino, "Palatino Linotype", "Palatino LT STD", Georgia, serif;
    font-weight: 300;
    letter-spacing: 0.1px;
    padding: 0;
    margin: 0px auto;
    background: white;
    color: white;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
}

html,
body {
    height: 100%;
    color: black;
}

.coming--soon {
    color: #fff;
    background-color: #303336;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;

    .coming--soon_body {
        display: flex;
        flex-direction: column;
        text-decoration: none;
        font-size: 1.5rem;
        color: rgb(255, 255, 255);
        height: 100vh;
        justify-content: center;
        z-index: 1;
        letter-spacing: 18px;
        .coming--soon_body_text {
            display: flex;
            flex-direction: column;
            align-items: center;
            cursor: pointer;
            .body_text--big-x {
                padding-bottom: 2rem;
                font-size: 4rem;
            }
            .body_text--silly {
                font-size: 15px;
                letter-spacing: normal;
                padding-top: 10px;
                width: 90%;
            }
        }
    }
    &:after {
        content:"";
        position: absolute;
        width:200vw;
        height: 200vh;
        left: -50%;
        top: -50%;
        background-image: url(data:image/png;base64,);
        animation:grain 1.5s steps(6) infinite;
    }
}

.extras {
    display: flex;
    flex-direction: column;
    cursor: default;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    position: fixed;
    top: 0;
    left: 0;
    padding: 1rem;

    .logo {
        display: flex;
        flex-direction: column;
        opacity: .75;
        -webkit-transition: opacity 250ms;
        transition: opacity 250ms;

        &:hover {
            opacity: 1;
        }

        span {
            width: 10px;
            height: 10px;
            background: black;
            margin: .2rem;
        }

        span:nth-of-type(1) {
            opacity: .75;
        }

        span:nth-of-type(2) {
            opacity: .50;
        }

        span:nth-of-type(3) {
            opacity: .25;
        }
    }
}

.blog {
    display: flex;
    justify-content: center;

    .post--list {
        display: flex;
        flex-direction: column;
        padding: 1em;

        .post {
            display: flex;
            flex-direction: column;
            align-items: center;
            max-width: 100%;
            line-height: 1.5;
            width: 36em;
            padding-bottom: 2rem;

            .post--img {
                display: flex;
                flex-direction: row;
                width: 100%;
                height: 20rem;
                background-size: cover;
                justify-content: flex-end;

                .meta-wrapper {
                    display: flex;
                    align-items: center;

                    .meta {
                        display: flex;
                        font-family: 'Thasadith', sans-serif;
                        color: white;
                        font-size: larger;
                        font-weight: 600;
                        -moz-transform: rotate(270deg);
                        /* FF3.5+ */
                        -o-transform: rotate(270deg);
                        /* Opera 10.5 */
                        -webkit-transform: rotate(-90deg);
                        /* Saf3.1+, Chrome */
                    }
                }
            }

            .post--info {
                padding: 1rem;
            }
        }
    }
}

.blog-post {
    display: flex;
    flex-direction: column;

    .blog-post--header {
        display: flex;
        flex-direction: column;
        text-transform: capitalize;
        background-size: cover;

        .blog-post--header-inner {
            background: rgba(65, 80, 92, 0.6);
        }

        .overlay--navbar {
            display: flex;
            flex-direction: row;
            padding: 0.7rem;

            .overlay--navbar-item {
                display: flex;
                width: 33%;

                a {
                    color: white;
                }
            }

        }

        .blog-post--header-overlay-container {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            color: white;

            height: 40rem;
            width: 100%;


            .overlay {
                display: flex;
                flex-direction: column;
                justify-content: center;
                max-width: 100%;

                font-family: Lora, serif;
                font-size: 2em;
                font-weight: 300;
                font-style: italic;

                .header-overlay--title {
                    padding-bottom: 1em;
                }

                .header-overlay--body {
                    display: flex;
                    flex-direction: row;
                    width: 100%;
                    font-size: 0.8rem;
                    font-style: normal;

                    .header-overlay--left {
                        display: flex;
                        flex-direction: column;
                        width: 25%;
                    }

                    .header-overlay--right {
                        display: flex;
                        flex-direction: column;
                        max-width: 75%;
                        width: 25rem;
                    }
                }
            }
        }
    }

    .blog-post--header-img-source {
        display: flex;
        color: grey;
        font-size: 13px;
        justify-content: center;
    }

    .blog-post--body {
        display: flex;
        width: 100%;
        justify-content: center;

        .blog-post--body-inner {
            display: flex;
            flex-direction: column;
            max-width: 100%;
            line-height: 1.5;
            font-size: 18px;
            width: 36em;
            padding-top: 3rem;

            p:first-child {
                &::first-letter {
                    line-height: 0.6em;
                    float: left;
                    font-size: 3em;
                    font-weight: bold;
                    color: #000;
                    margin-right: 10px;
                    margin-top: 8px;
                }
            }
        }
    }
}

.fullscreen-background {
    width: 100%;
    height: 100%;
    overflow: hidden;

    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-position: 50% 50%\9 !important;
    background-size: cover;
}

.overlay-black {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    background: radial-gradient(ellipse at center, #4650586e 0%, #35373aa8 100%);
    position: absolute;
    overflow: hidden;
}
.overlay-black::after {
    content:"";
    position: absolute;
    width:200vw;
    height: 200vh;
    left: -50%;
    top: -50%;
    background-image: url(data:image/png;base64,);
    animation:grain 1.5s steps(6) infinite;
}

.grain-img {
  z-index: -1;
}

@keyframes grain {
  0%, 100% {transform: translate(0,0);}
  10% {transform: translate(-5%,-10%);}
  30% {transform: translate(3%,-15%);}
  50% {transform: translate(12%,9%);}
  70% {transform: translate(9%,4%);}
  90% {transform: translate(-1%,7%);}
}

.overlay-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;
    line-height: normal;
    z-index: 1;

    .main-body {
        display: flex;
        flex-direction: column;
        text-decoration: none;
        font-size: 8rem;
        color: rgba(0, 0, 0, 0.4);
        height: 80vh;
        justify-content: center;
        .logo-inner {
            display: flex;
            flex-direction: row;
        }
    }

    .links {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    .link {
        margin: 15px;

        a {
            color: #ffffffa1;
        }
    }

    .nav-bar {
        display: flex;
        align-self: flex-end;
        color: white;
        font-size: 15px;
        letter-spacing: 2px;
        padding: 1rem;
        text-transform: lowercase;

        a {
            color: white;
            text-decoration: none;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}

@media screen and (max-width:800px) {
    .overlay-content {
        .logo {
            text-decoration: none;
            font-size: 6rem;
        }
    }
}

@media screen and (max-width:400px) {
    .overlay-content {
        .logo {
            text-decoration: none;
            font-size: 5rem;
            color: rgba(0, 0, 0, 0.4);
        }
    }
}
